<template>
  <div class="app-container adminuser-conlog">
    <div class="title">
      【<span class="name">{{ name }}</span
      >】登录日志列表
    </div>
    <el-table
      :header-row-style="getHeadStyle"
      :data="tableData"
      style="margin-top:30px;width: 100%;"
    >
      <el-table-column
        align="center"
        prop="insertTime"
        label="时间"
        width="180"
        style="text-align: center;"
      />
      <el-table-column align="center" prop="ip" label="IP" />
      <el-table-column align="center" prop="area" label="地区" />
      <el-table-column align="center" prop="browser" label="登录方式" />
      <template slot="empty">
        <div class="table-empty">
          暂无数据
        </div>
      </template>
    </el-table>
    <el-pagination
      style=" margin-top:55px;text-align: center;"
      :current-page="listQuery.currentPage"
      :page-size="listQuery.pageSize"
      layout="total, prev, pager, next"
      :total="tableCount"
      @current-change="handleCurrentChange"
    />
  </div>
</template>

<script>
import { getConlogList } from '@/api/adminUser'
export default {
  name: 'AdminUserConlog',
  data() {
    return {
      name: '',
      id: '',
      listQuery: {
        pageSize: 15,
        currentPage: 1
      },
      tableCount: 0,
      tableData: []
    }
  },
  mounted() {
    this.name = this.$route.query.name
    this.id = this.$route.params.id
    this.handlerGetList(this.id, this.listQuery)
  },
  methods: {
    handlerGetList(id, query) {
      getConlogList(id, query).then(result => {
        const arr = result.data.list.map(item => {
          item.insertTime = this.formatTime(item.insertTime)
          item.browser = item.browser ? item.browser : '-'
          return item
        })
        this.tableData = arr
        this.tableCount = result.data.count
      })
    },

    handleCurrentChange(e) {
      this.listQuery.currentPage = e
      this.handlerGetList(this.id, this.listQuery)
    },

    getHeadStyle() {
      return {
        fontSize: '14px',
        color: '#17233D',
        letterSpacing: 0
      }
    }
  }
}
</script>

<style lang="scss">
.adminuser-conlog {
  .title {
    font-size: 20px;
    color: #17233d;
    .name {
      line-height: 20px;
      color: #0085ff;
      letter-spacing: 0;
    }
  }
  .table-empty {
    font-size: 16px;
    color: #576277;
    letter-spacing: 0;
  }
}
</style>
